import {Injectable} from '@angular/core';
import {FormControlSize} from '../form-control/index';

@Injectable({
  providedIn: 'root'
})
export class AdaptRxTextareaConfigService {
  size: FormControlSize = 'default';
  rows: number;
  maxRows: number;
  showValidState: boolean = false;
  showValidStateIcon: boolean = false;
  showInvalidStateIcon: boolean = false;
  disabledStyleForReadonlyState: boolean = false;
}
